@import url('https://fonts.googleapis.com/css2?family=Ysabeau+SC:wght@200&display=swap');

.App {
}
@keyframes glowing {
  0% {
    text-shadow: 0 0 1px #ffffff;
  }
  25% {
    text-shadow: 0 0 5px #f3f4ed, 0 0 10px #f3f4ed;
  }
  50% {
    text-shadow: 0 0 15px #f3f4ed, 0 0 20px #f3f4ed;
  }
  75% {
    text-shadow: 0 0 25px #f3f4ed, 0 0 30px #f3f4ed;
  }
  100% {
    text-shadow: 0 0 5px #fff;
    color: #f3f4ed;
  }
}

@keyframes entry {
  0% {
    font-size: 1vw;
  }
  25% {
    font-size: 3vw;
  }
  50% {
    font-size: 5vw;
  }
  75% {
    font-size: 8vw;
  }
  100% {
    font-size: 10vw;
  }
}

.main-heading {
  font-size: 25vw;
  text-align: center;
  color: #f3f4ed;
  font-family: 'Ysabeau SC', sans-serif;
  animation: glowing 3s infinite, entry 2s 0;
  /* animation-name: entry;
  animation-duration: 3s;
  animation-iteration-count: 1; */
}

.main-heading-animation {
  animation-name: entry;
  animation-duration: 3s;
  animation-iteration-count: 1;
}

